html {
  font-size: 62.5%;
}

:focus {
  outline: none !important;
  .Demo[data-user-interaction='keyboard'] & {
    outline: 2px solid #dc8e3b !important;
    outline-offset: -1px;
  }
}

figure {
  background-color: #61dafb;
  width: 640px;
  max-width: calc(100vw - 3rem);
  margin: 0;
  position: relative;

  h3 {
    font-weight: 700;
  }
  > a {
    display: block;
  }
  img {
    width: 100%;
    opacity: 0.66;
    transform: translate3d(0, 0, 0);
    transition: transform 0.3s ease;
  }
  figcaption {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 198px;
    padding: 20px;
    background-color: #1d1d1d;
    color: #fff;
    transform: translate3d(0, 198px, 0);
    transition: transform 0.3s ease;
    a {
      color: #fff;
    }
    .description {
      margin-bottom: 0;
    }
  }
  @at-root {
    .Demo[data-user-interaction='mouse'] #{&} {
      overflow: hidden;
    }
    .Demo[data-user-interaction='mouse'] #{&}:hover figcaption {
      transform: translate3d(0, 0, 0);
    }
    .Demo[data-user-interaction='mouse'] #{&}:hover img {
      transform: translate3d(0, -198px, 0);
      transition: transform 0.3s ease;
    }
    .Demo[data-user-interaction='touch'] #{&} figcaption,
    .Demo[data-user-interaction='keyboard'] #{&} figcaption {
      transform: none;
      position: relative;
      bottom: auto;
      left: auto;
      height: auto;
    }
  }
}

.Demo {
  font-size: 16px;
  text-align: center;
  background-color: #f7f7f7;

  header {
    z-index: 1;
    position: sticky;
    top: 0;
    background-color: #282c34;
    height: 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    line-height: 1.5rem;
    color: white;

    .React-logo {
      height: 4rem;
      pointer-events: none;

      @media (prefers-reduced-motion: no-preference) {
        animation: React-logo-spin infinite 20s linear;
      }
    }
  }

  main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 18rem);

    section {
      margin: 3rem 0;

      h2 {
        margin: 15px 0;
        font-weight: 700;
        font-size: 30px;
      }
    }

    form {
      display: flex;
      flex-direction: column;
      align-items: center;

      > * + * {
        margin-top: 0.5rem;
      }

      input,
      select,
      textarea,
      button {
        width: 640px;
        max-width: calc(100vw - 3rem);
        height: 4rem;
        padding: 1rem;
        display: block;
        border: 1px solid #ccc;
      }

      textarea {
        height: 12rem;
      }

      button {
        border: 1px solid #323232;
        background-color: #323232;
        color: #ffffff;
      }
    }
  }

  footer {
    position: sticky;
    bottom: 0;
    background-color: #282c34;
    width: 100%;
    height: 12rem;
    padding: 0 3rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    font-size: 1.6rem;
    line-height: calc(1.6rem * 1.5);
    color: white;
    text-align: left;
  }
}

@keyframes React-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
